/* PartList.css */

.partLabel {
    color: white; /* Header text color */
    padding-bottom: 8px;
    margin-bottom: 16px;
    font-size: 24px;
}
  
.section-links {
    display: flex;
    justify-content: space-around;
    background-color: #f8f9fa; /* Light background color */
    padding: 10px;
    border-radius: 5px;
    box-shadow: 0px 2px 5px rgba(0, 0, 0, 0.1); /* Subtle box shadow */
}
  
.section-links button {
    text-decoration: none;
    background: none;
    border:none;
    cursor: pointer;
    color: #495057; /* Text color */
    padding: 8px 12px;
    border-radius: 5px;
    transition: background-color 0.3s ease-in-out, color 0.3s ease-in-out;
}
  
.section-links button:hover {
    background-color: #007bff; /* Hover background color */
    color: #fff; /* Hover text color */
}

.section-links button.active {
    background-color: #007bff; /* Active link color */
    color:white;
}

.part-container {
  height: 77.6vh;
  overflow-y: scroll;
}
  
  
  /* PartList.css */

.part-list {
    opacity: 1;
    transition: opacity 0.2s ease-in-out;
}
  
.part-list.fade-out {
    opacity: 0;
}
  
  